import { createEntityAdapter, EntityAdapter, EntityState } from '@ngrx/entity';
import { Action, ActionReducer, createReducer, on } from '@ngrx/store';
import { Expense } from './expenses.model';
import * as expenseActions from './expenses.actions';


export const expensesFeatureKey = 'expenses';

export interface State extends EntityState<Expense> {
  loading: boolean;
  loaded: boolean;
}

export const adapter: EntityAdapter<Expense> = createEntityAdapter<Expense>();

export const initialState: State = adapter.getInitialState({
  loading: false,
  loaded: false
});


export const expenseReducer = createReducer(
  initialState,
  on(expenseActions.StartLoading, (state: State) => {
    return { ...state, loading: true };
  }),
  on(expenseActions.DoneLoading, (state: State) => {
    return { ...state, loaded: true };
  }),
  on(expenseActions.addExpense, (state: State, { expense }) => {
    return adapter.addOne(expense, state);
  }),
  on(expenseActions.addExpenses, (state: State, { expenses }) => {
    return adapter.addMany(expenses, state);
  })
);

export function reducer(state: State | undefined, action: Action): any {
  return expenseReducer(state, action);
}

export const selectLoading = (state: State) => state.loading;
export const selectLoaded = (state: State) => state.loaded;

export const {
  selectIds,
  selectEntities,
  selectAll,
} = adapter.getSelectors();
