import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';
import { TrackerService } from 'src/app/services/tracker.service';
import { Expense } from 'src/app/store/finance-accounting/expenses/expenses.model';
import { Income } from 'src/app/store/finance-accounting/income/income.model';

@Component({
  selector: 'app-balance-sheet',
  templateUrl: './balance-sheet.component.html',
  styleUrls: ['./balance-sheet.component.scss']
})
export class BalanceSheetComponent implements OnInit {
  startDate = new Date();
  endDate = new Date();
  incomes: Income[] = [];
  expenses: Expense[] = [];
  expenseTotal = 0;
  incomeTotal = 0;
  constructor(private trackerService: TrackerService) { }

  ngOnInit(): void {
    this.getData().then();
  }

  async getData() {
    const startDate = moment(this.startDate).format('YYYY-MM-DD');
    const endDate = moment(this.endDate).format('YYYY-MM-DD');
    this.incomes = await this.trackerService.getEvents('Income', 'm0frOspS7JY', 'Income', 'DESCENDANTS').toPromise();
    this.expenses = await this.trackerService.getEvents('Expense', 'm0frOspS7JY', 'Expense', 'DESCENDANTS').toPromise();
    this.expenseTotal = this.expenses.map(expense => expense.expenseAmount).reduce((a, b) => a + b);
    this.incomeTotal = this.incomes.map(income => income.incomeAmount).reduce((a, b) => a + b);
  }

}
