import { createAction, props } from '@ngrx/store';
import { Expense } from './expenses.model';

export const loadExpensess = createAction(
  '[Expenses] Load Expensess'
);

export const addExpense = createAction('[Expense] Add Expense', props<{ expense: Expense }>());
export const addExpenses = createAction('[Expenses] Add expenses', props<{ expenses: Expense[] }>());
export const GetExpenses = createAction('[Expenses] Get expenses');
export const StartLoading = createAction('[Expenses] Initiate loading');
export const DoneLoading = createAction('[Expenses] Done Loading');




