import { AfterViewInit, Component, ViewChild, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';

@Component({
  selector: 'app-data-table',
  templateUrl: './data-table.component.html',
  styleUrls: ['./data-table.component.scss']
})
export class DataTableComponent implements OnInit, OnChanges {
  @Output() add = new EventEmitter();
  @Input() tableList: any[];
  @Input() loading = false;
  @Input() tableConfigurations: {
    showAddButton: boolean;
    addText: string;
    tableColumns: { label: string, name: string, type?: string }[]
  };
  displayedColumns: string[];
  dataSource: MatTableDataSource<any[]>;
  constructor() { }

  ngOnInit(): void {
    this.initData();
  }

  ngOnChanges(): void {
    this.initData();
  }

  initData(): void {
    this.displayedColumns = ['index', ...this.tableConfigurations.tableColumns.map(col => col.name)];
    this.dataSource = new MatTableDataSource<any[]>(this.tableList);
  }

  onAdd(): void {
    this.add.emit();
  }

}
