import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { AddEditExpensesComponent } from './add-edit-expenses/add-edit-expenses.component';
import * as expenseSelector from '../../../../store/finance-accounting/expenses/expenses.selectors';
import { Expense } from '../../../../store/finance-accounting/expenses/expenses.model';
import { Observable } from 'rxjs';
import { AppState } from 'src/app/store';
import { Store } from '@ngrx/store';
import { TrackerService } from 'src/app/services/tracker.service';
import { addExpenses } from 'src/app/store/finance-accounting/expenses/expenses.actions';
import { fadeIn } from 'src/app/shared/animations/router-animation';

@Component({
  selector: 'app-expenses',
  templateUrl: './expenses.component.html',
  styleUrls: ['./expenses.component.scss'],
  animations: [fadeIn]
})
export class ExpensesComponent implements OnInit {
  expenses$: Observable<Expense[]>;
  expenses: Expense[] = [];
  tableConfigurations = {
    showAddButton: true,
    addText: 'New Expense',
    tableColumns: [
      { label: 'Expense Date', name: 'expenseDate' },
      { label: 'Expense From', name: 'expenseFrom' },
      { label: 'Amount', name: 'expenseAmount', type: 'number' },
      { label: 'Description', name: 'expenseDescription' }
    ]
  };
  loading = false;
  constructor(private dialog: MatDialog, private store: Store<AppState>, private trackerService: TrackerService) { }

  ngOnInit(): void {
    this.expenses$ = this.store.select(expenseSelector.selectAll);
    this.getData().then();
  }

  async getData(): Promise<void> {
    this.loading = true;
    const expenses: Expense[] = await this.trackerService.getEvents('Expense', 'm0frOspS7JY', 'Expense', 'DESCENDANTS').toPromise();
    this.store.dispatch(addExpenses({ expenses }));
    this.loading = false;
  }

  onAdd(): void {
    this.dialog.open(AddEditExpensesComponent, {
      width: '90%',
      disableClose: true
    });
    console.log('delete');
  }

}
