import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Store } from '@ngrx/store';
import * as moment from 'moment';
import { CommonService } from 'src/app/services/common.service';
import { TrackerService } from 'src/app/services/tracker.service';
import { addExpense } from 'src/app/store/finance-accounting/expenses/expenses.actions';
import { Expense } from 'src/app/store/finance-accounting/expenses/expenses.model';
import { AppState } from '../../../../../store';

@Component({
  selector: 'app-add-edit-expenses',
  templateUrl: './add-edit-expenses.component.html',
  styleUrls: ['./add-edit-expenses.component.scss']
})
export class AddEditExpensesComponent implements OnInit {
  expenseForm: FormGroup;
  savingData = false;
  constructor(
    private formBuilder: FormBuilder,
    private dialog: MatDialog,
    private store: Store<AppState>,
    private commonService: CommonService,
    private trackerService: TrackerService
  ) { }

  ngOnInit(): void {
    this.expenseForm = this.formBuilder.group({
      expenseDate: [''],
      expenseFrom: [''],
      amount: [''],
      description: [''],
    });

    this.expenseForm.patchValue({
      expenseDate: new Date()
    });
  }

  async onSave(): Promise<void> {
    this.savingData = true;
    const formValue = this.expenseForm.value;
    const expenseDate = moment(formValue.expenseDate).format('YYYY-MM-DD');
    const expense: Expense = {
      id: this.commonService.makeId(),
      expenseDate,
      expenseAmount: formValue.amount,
      expenseDescription: formValue.description,
      expenseFrom: formValue.expenseFrom
    };
    try {
      const payload = this.trackerService.prepareEvents('Expense', 'm0frOspS7JY', null, 'Expense', null, expense);
      await this.trackerService.saveEvent(payload).toPromise();
      // console.log('payload', payload);
    } catch {

    }
    this.store.dispatch(addExpense({ expense }));
    this.savingData = false;
    this.onCancel();
  }

  onCancel(): void {
    console.log('closing');
    this.dialog.closeAll();
  }

}
