import { createAction, props } from '@ngrx/store';
import { Income } from './income.model';

export const loadIncomes = createAction(
  '[Income] Load Incomes'
);

export const addIncome = createAction('[Income] Add Income', props<{ income: Income }>());
export const addIncomes = createAction('[Income] Add Incomes', props<{ incomes: Income[] }>());
export const getIncomes = createAction('[Income] getIcome');
export const startLoading = createAction('[Income] initiate loading');
export const doneLoading = createAction('[Income] done loading');
