import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { TrackerService } from 'src/app/services/tracker.service';
import { AppState } from 'src/app/store';
import { addIncomes } from 'src/app/store/finance-accounting/income/income.actions';
import { Income } from 'src/app/store/finance-accounting/income/income.model';
import * as incomeSelector from '../../../../store/finance-accounting/income/income.selectors';
import { AddEditIncomeComponent } from './add-edit-income/add-edit-income.component';

@Component({
  selector: 'app-income',
  templateUrl: './income.component.html',
  styleUrls: ['./income.component.scss']
})
export class IncomeComponent implements OnInit {
  incomes$: Observable<Income[]>;
  loading = false;
  tableConfigurations = {
    showAddButton: true,
    addText: 'Income',
    tableColumns: [
      { label: 'Income Date', name: 'incomeDate' },
      { label: 'Income From', name: 'incomeFrom' },
      { label: 'Amount', name: 'incomeAmount', type: 'number' },
      { label: 'Description', name: 'incomeDescription' }
    ]
  };
  constructor(private store: Store<AppState>, private dialog: MatDialog, private trackerService: TrackerService) { }

  ngOnInit(): void {
    this.incomes$ = this.store.select(incomeSelector.selectAll);
    this.getData().then();
  }

  onAdd(): void {
    this.dialog.open(AddEditIncomeComponent, {
      width: '90%',
      disableClose: true
    });
  }

  async getData() {
    this.loading = true;
    const incomes: Income[] = await this.trackerService.getEvents('Income', 'm0frOspS7JY', 'Income', 'DESCENDANTS').toPromise();
    this.store.dispatch(addIncomes({ incomes }));
    this.loading = false;
  }

}
