import { createEntityAdapter, EntityAdapter, EntityState } from '@ngrx/entity';
import { Action, createReducer, on } from '@ngrx/store';
import { Income } from './income.model';
import * as incomeActions from './income.actions';


export const incomeFeatureKey = 'income';

export interface State extends EntityState<Income> {
  loading: boolean;
  loaded: boolean;
}

export const adapter: EntityAdapter<Income> = createEntityAdapter<Income>();

export const initialState: State = adapter.getInitialState({
  loading: false,
  loaded: false
});


export const incomeReducer = createReducer(
  initialState,
  on(incomeActions.startLoading, (state: State) => {
    return { ...state, loading: true };
  }),
  on(incomeActions.doneLoading, (state: State) => {
    return { ...state, loading: false, loaded: true }
  }),
  on(incomeActions.addIncome, (state: State, { income }) => {
    return adapter.addOne(income, state);
  }),
  on(incomeActions.addIncomes, (state: State, { incomes }) => {
    return adapter.addMany(incomes, state);
  })
);

export const selectLoading = (state: State) => state.loading;
export const selectLoaded = (state: State) => state.loaded;

export const {
  selectAll,
  selectIds,
  selectEntities
} = adapter.getSelectors();

export function reducer(state: State | undefined, action: Action): any {
  return incomeReducer(state, action);
}

