import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClientService } from './http-client.service';
import { ManifestService } from './manifest.service';

let authenticationtoken;

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private manifestService: ManifestService, private http: HttpClient) { }

  removeLocalStorageItem(key): void {
    localStorage.removeItem(`${key}`);
  }

  prepareToken(credentials: { username; password }): string {
    const { username, password } = credentials;
    const token = btoa(username + ':' + password);
    return token;
  }

  createDHISAuthorizationHeader(token): any {
    const headers = new HttpHeaders({ Authorization: 'Basic ' + token });
    return headers;
  }

  login(credentials: { username; password }): Observable<string> {
    const meUrl =
      'me.json?fields=id,name,displayName,created,lastUpdated,email,dataViewOrganisationUnits[id,name,level,parent[id,name]],organisationUnits[id,name,level,parent[id,name]],userGroups[id,name],userCredentials[username,lastLogin,userRoles[authorities]]';
    return new Observable(observer => {
      const token = this.prepareToken(credentials);
      authenticationtoken = token;
      // this.removeLocalStorageItem('facility-tool-web-token');
      const headers: HttpHeaders = this.createDHISAuthorizationHeader(token);
      this.manifestService.getRootUrl().subscribe((rootUrl) => {
        this.http.get(rootUrl + meUrl, { headers }).subscribe(
          (data) => {
            console.log('data', data);
            localStorage.setItem('token', token);
            localStorage.setItem('current-user', JSON.stringify(data));
            // localStorage.setItem(
            //   'facility-tool-user-organisation-unit',
            //   // this.getUserOu(data)
            // );
            observer.next('Login successful..');
            observer.complete();
          },
          (error1) => {
            const errorMessage = error1.message;
            console.log({ errorMessage });
            if (errorMessage.indexOf('Unauthorized') > -1) {
              observer.error('Incorrect Username or Password');
            } else if (errorMessage.toLowerCase().indexOf('unknown') > -1) {
              observer.error('There is no internet connection');
            } else {
              observer.error(
                'There is problem with server please contact the administrator'
              );
            }
          }
        );
      });
    });
  }
}
