import { createFeatureSelector, createSelector } from '@ngrx/store';
import * as fromReducer from './expenses.reducer';

export const selectExpenseState = createFeatureSelector<fromReducer.State>('expenses');
export const selectAll = createSelector(selectExpenseState, fromReducer.selectAll);
export const selectEntities = createSelector(selectExpenseState, fromReducer.selectEntities);
export const selectLoading = createSelector(selectExpenseState, fromReducer.selectLoading);
export const selectLoaded = createSelector(selectExpenseState, fromReducer.selectLoaded);
