import { NgModule } from '@angular/core';
import { Routes, RouterModule, PreloadAllModules } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { BuildComponent } from './modules/building-real-estates/build/build.component';
import { FinanceComponent } from './modules/finance-accounting/finance/finance.component';
import { ShopsComponent } from './modules/shops-hardware/shops/shops.component';
import { TransportationComponent } from './modules/transportation-logistic/transportation/transportation.component';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent,
    children: [
      {
        path: '',
        component: FinanceComponent
      },
      {
        path: 'finance-accounting',
        loadChildren: () =>
          import(
            './modules/finance-accounting/finance-accounting.module'
          ).then((m) => m.FinanceAccountingModule),
        data: { state: 'finance-accounting' },
      },

      {
        path: 'transportation-logistic',
        loadChildren: () =>
          import(
            './modules/transportation-logistic/transportation-logistic.module'
          ).then((m) => m.TransportationLogisticModule),
        data: { state: 'transportation-logistic' },
      },
      {
        path: 'shops-hardware',
        loadChildren: () =>
          import(
            './modules/shops-hardware/shops-hardware.module'
          ).then((m) => m.ShopsHardwareModule),
        data: { state: 'shops-hardware' },
      },
      {
        path: 'building-real-estates',
        loadChildren: () =>
          import(
            './modules/building-real-estates/building-real-estates.module'
          ).then((m) => m.BuildingRealEstatesModule),
        data: { state: 'building-real-estates' },
      },
    ]
  },
  {
    path: 'login',
    component: LoginComponent
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      useHash: true,
      preloadingStrategy: PreloadAllModules,
      relativeLinkResolution: 'legacy'
    })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
