import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CommonService {

  constructor() { }

  makeId(): string {
    let text = '';
    const firstLetterPossibleCombination =
      'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz';
    const PossibleCombination =
      'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    for (let i = 0; i < 11; i++) {
      if (i === 0) {
        text += firstLetterPossibleCombination.charAt(
          Math.floor(Math.random() * firstLetterPossibleCombination.length)
        );
      } else {
        text += PossibleCombination.charAt(
          Math.floor(Math.random() * PossibleCombination.length)
        );
      }
    }
    return text;
  }
}
