import {
  ActionReducer,
  ActionReducerMap,
  createFeatureSelector,
  createSelector,
  MetaReducer
} from '@ngrx/store';
import { environment } from '../../environments/environment';
import * as fromExpenseReducer from './finance-accounting/expenses/expenses.reducer';
import * as fromIncomeReducer from './finance-accounting/income/income.reducer';


export interface AppState {
  expenses: fromExpenseReducer.State;
  income: fromIncomeReducer.State;
}

export const reducers: ActionReducerMap<AppState> = {
  expenses: fromExpenseReducer.reducer,
  income: fromIncomeReducer.reducer
};


export const metaReducers: MetaReducer<AppState>[] = !environment.production ? [] : [];
