import { createFeatureSelector, createSelector } from '@ngrx/store';
import { Income } from './income.model';
import * as fromReducer from './income.reducer';

export const selectIncomeState = createFeatureSelector<fromReducer.State>('income');

export const selectAll = createSelector(selectIncomeState, fromReducer.selectAll);
export const selectEntities = createSelector(selectIncomeState, fromReducer.selectEntities);

