import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Store } from '@ngrx/store';
import * as moment from 'moment';
import { Income } from '../../../../../store/finance-accounting/income/income.model';
import { CommonService } from '../../../../../services/common.service';
import { AppState } from '../../../../../store';
import { addIncome } from '../../../../../store/finance-accounting/income/income.actions';
import { TrackerService } from 'src/app/services/tracker.service';

@Component({
  selector: 'app-add-edit-income',
  templateUrl: './add-edit-income.component.html',
  styleUrls: ['./add-edit-income.component.scss']
})
export class AddEditIncomeComponent implements OnInit {
  incomeForm: FormGroup;
  savingData = false;
  constructor(
    private formBuilder: FormBuilder,
    private store: Store<AppState>,
    private dialog: MatDialog,
    private commonService: CommonService,
    private trackerService: TrackerService
  ) { }

  ngOnInit(): void {
    this.incomeForm = this.formBuilder.group({
      incomeDate: [''],
      incomeFrom: [''],
      amount: [''],
      description: ['']
    });

    this.incomeForm.patchValue({
      incomeDate: new Date()
    });
  }

  async onSave(): Promise<void> {
    this.savingData = true;
    const formValue = this.incomeForm.value;
    const incomeDate = moment(formValue.incomeDate).format('YYYY-MM-DD');
    const income: Income = {
      id: this.commonService.makeId(),
      incomeDate,
      incomeAmount: formValue.amount,
      incomeFrom: formValue.incomeFrom,
      incomeDescription: formValue.description
    };
    try {
      const incomePayload = this.trackerService.prepareEvents('Income', 'm0frOspS7JY', null, 'Income', null, income);
      await this.trackerService.saveEvent(incomePayload).toPromise();
      this.store.dispatch(addIncome({ income }));
    } catch (e) {
      console.error('Error', e);
    }
    this.savingData = false;
    this.onCancel();
  }

  onCancel(): void {
    this.dialog.closeAll();
  }

}
