import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { routeAnimations } from '../shared/animations/router-animation';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  animations: [routeAnimations]
})
export class HomeComponent implements OnInit {
  menus = [
    {
      name: 'Finance & Accounting',
      route: '/finance-accounting',
      icon: 'coin.png'
    },
    {
      name: 'Transportation & Logistic',
      route: '/transportation-logistic',
      icon: 'public-transport.png'
    },
    {
      name: 'Building & Real estates',
      route: '/building-real-estates',
      icon: 'building.png'
    },
    {
      name: 'Shops & Hardware',
      route: '/shops-hardware',
      icon: 'shop.png'
    }
  ];

  currentUser;
  constructor(private router: Router) { }

  ngOnInit(): void {
    const user = localStorage.getItem('current-user');
    this.currentUser = JSON.parse(user);
    console.log(this.currentUser);
    console.log('home page');
  }

  onLogout() {
    localStorage.removeItem('token');
    localStorage.removeItem('current-user');
    this.router.navigate(['login']);
  }

}
